.wrapItem {
    border: 1.15px solid #f1f1f1;
    border-radius: 27px;
    padding: 32px;
    width: 100%;
    /* height: 126px;  */
    background: #e7f1ed;
    text-decoration: none;
display: flex;
flex-direction: column;
gap: 18px;
box-sizing: border-box;
position: relative;
overflow: hidden;
}
.wrapItem:hover {
    border: 1.15px solid #caf1e2;  
} 

.titleStar {
    display: flex;
    align-items: flex-start;
    gap: 37px;
}

.title {
font-weight: 600;
font-size: 16px;
line-height: 140%;
color: #1d1e21;
margin: 0;
overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  line-clamp: inherit;
  -webkit-box-orient: vertical;
  white-space: normal;
  /* width: calc(100% - 20px); */

  -moz-box-orient: vertical;
}

.star {
    display: flex;
    align-items: center;
    gap: 6px; 
}
.star span {
    font-weight: 500;
    font-size: 14px;
    line-height: 129%;
    color: #1d1e21; 
}

.open {
    border-radius: 8px;
    /* padding: 8px 16px; */
    width: 65px;
    height: 34px; 
    background: rgba(89, 177, 122, 0.1);
font-weight: 600;
font-size: 12px;
line-height: 150%;
letter-spacing: -0.02em;
text-transform: uppercase;
text-align: center;
color: #59b17a;
display: flex;
align-items: center;
justify-content: center;

}

.addresPhone {
    display: flex;
    /* align-items: center; */
    gap: 8px;
    /* justify-content: center;  */
font-size: 14px;
line-height: 129%;
color: #93939a;

}
.addresPhone p {
    margin: 0;
}
